html {
  font-family: "Open Sans", sans-serif; /* this should be the rest of the output you got from Google Fonts */
}

 
html {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: #181818;
  color: white;
}

.container {
  margin: 1rem;
  text-align: center;
}

.nav {
  background-color: #000000;
  color: white;
  display: flex;
  justify-content: space-evenly;
  align-items: start;
  gap: 10%;
}

.site-title {
  font-size: 2rem;
}

.nav ul {
  font-size: 2rem;
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  gap: 10%;
}

.nav a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0.25rem;
}

.nav li.active {
  /*background-color: #222222;*/
  color: red;
}

.nav li:hover {
  /*background-color: #141414;*/
  color: rgb(141, 48, 48);
}

